import $ from "jquery";

export default class BaseApiClient {
  baseUrl = "";

  get(path, { data, additionalRequestParams } = {}) {
    return this._request({
      method: "GET",
      path,
      data,
      additionalRequestParams
    });
  }

  post(path, { data, additionalRequestParams } = {}) {
    return this._request({
      method: "POST",
      path,
      data,
      additionalRequestParams
    });
  }

  patch(path, { data, additionalRequestParams } = {}) {
    return this._request({
      method: "PATCH",
      path,
      data,
      additionalRequestParams
    });
  }

  delete(path, { data, additionalRequestParams } = {}) {
    return this._request({
      method: "DELETE",
      path,
      data,
      additionalRequestParams
    });
  }

  _request({
    method = "GET",
    path = "",
    url = null,
    data = {},
    additionalRequestParams = {},
    headers = {}
  }) {
    const requestParams = {
      method,
      url: url || `${this.baseUrl}${path}`,
      headers,
      data: this._wrapData(data)
    };

    return $.ajax({ ...requestParams, ...additionalRequestParams });
  }

  _wrapData(data, wrapperKey = this.wrapParamsKey) {
    if (!wrapperKey) return data;

    const wrappedData = {};
    wrappedData[wrapperKey] = data;

    return wrappedData;
  }

  createFormData(fieldName, fieldValue) {
    const formData = new FormData();
    formData.append(fieldName, fieldValue);

    return formData;
  }

  formDataRequest({ path, formData, method = "PATCH" }) {
    return this._request({
      method,
      path,
      data: formData,
      additionalRequestParams: {
        contentType: false,
        processData: false
      }
    });
  }
}
