import React, { PureComponent } from "react";
import CustomerPaymentContainer from "containers/CustomerPaymentContainer";
import PaymentMethodApiClient from "api/PaymentMethodApiClient";
import TransactionApiClient from "api/TransactionApiClient";

export default class CustomerPaymentPage extends PureComponent {
  state = {
    transactions: []
  };

  paymentMethodApiClient = new PaymentMethodApiClient();

  transactionApiClient = new TransactionApiClient();

  componentDidMount() {
    this.paymentMethodApiClient
      .getAllMethods()
      .then(res => console.log("res", res));
    this.transactionApiClient
      .getAllTransactions()
      .then(transactions => this.setState({ transactions }));
  }

  render() {
    const { transactions } = this.state;
    return <CustomerPaymentContainer transactions={transactions} />;
  }
}
