import React from "react";
import PropTypes from "prop-types";
import Modal from "@material-ui/core/Modal";
import Button from "@material-ui/core/Button";
import { toCurrency } from "Utils/numberFormat";
import styles from "./styles.module.scss";
import qrWechar from "./qr_wechat.png";
import qrAlipay from "./qr_alipay.png";

export default function PaymentModal({ isOpen, toggle, method, amount }) {
  let qr = qrWechar;
  if (method === "alipay") {
    qr = qrAlipay;
  }
  return (
    <Modal open={isOpen} onClose={toggle}>
      <div className={styles.modalContainer}>
        <div className={styles.modalTitle}>
          <h2>{`Pay with ${method}`}</h2>
          <h2>{toCurrency(amount)}</h2>
        </div>
        {method !== "bank" && (
          <img className={styles.qrImage} src={qr} alt="qr" />
        )}
        <div className={styles.modalFooter}>
          <Button variant="contained" color="primary" onClick={toggle}>
            Complete
          </Button>
        </div>
      </div>
    </Modal>
  );
}

PaymentModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  toggle: PropTypes.func.isRequired,
  method: PropTypes.string.isRequired,
  amount: PropTypes.string.isRequired
};
