import React from "react";
import PropTypes from "prop-types";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Button from "@material-ui/core/Button";
import SectionHeader from "components/SectionHeader";
import withStyles from "@material-ui/core/styles/withStyles";
import wechatIcon from "./wechat.png";
import alipayIcon from "./alipay.png";
import styles from "./styles.module.scss";

const StyledRadioGroup = withStyles(() => ({
  root: {
    flexDirection: "row",
    alignItems: "center",
    margin: "30px 0"
  }
}))(RadioGroup);

const StyledFormControlLabel = withStyles(() => ({
  root: {}
}))(FormControlLabel);

export default function MethodSection({ method, amount, onChange, onPay }) {
  return (
    <>
      <SectionHeader title="Method" />
      <StyledRadioGroup
        aria-label="gender"
        name="gender1"
        value={method}
        onChange={onChange}
      >
        <StyledFormControlLabel
          value="wechat"
          control={<Radio />}
          label={
            <img
              className={styles.imgLabel}
              src={wechatIcon}
              alt="wechat-pay"
            />
          }
        />
        <StyledFormControlLabel
          value="alipay"
          control={<Radio />}
          label={
            <img className={styles.imgLabel} src={alipayIcon} alt="alipay" />
          }
        />
        <StyledFormControlLabel
          value="bank"
          control={<Radio />}
          label="BANK TRANSFER"
        />
        <Button
          variant="contained"
          color="primary"
          onClick={onPay}
          disabled={!amount || !method}
        >
          Pay
        </Button>
      </StyledRadioGroup>
    </>
  );
}

MethodSection.propTypes = {
  method: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  onPay: PropTypes.func.isRequired,
  amount: PropTypes.string.isRequired
};
