import React from "react";
import PropTypes from "prop-types";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import SectionHeader from "components/SectionHeader";
import moment from "moment";
import { toCurrency } from "Utils/numberFormat";
import { DEFAULT_DATE_TIME_FORMAT } from "Utils/setting";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  createAt: {
    width: "25%"
  }
});

export default function TransactionHistorySection({ transactions }) {
  const classes = useStyles();
  return (
    <>
      <SectionHeader title="Transaction History" />
      <Table>
        <TableBody>
          {transactions.map(
            ({
              _id,
              createdAt,
              status,
              amount,
              paymentMethod: { name: paymentName }
            }) => (
              <TableRow key={_id}>
                <TableCell className={classes.createAt}>
                  {moment(createdAt).format(DEFAULT_DATE_TIME_FORMAT)}
                </TableCell>
                <TableCell>{paymentName}</TableCell>
                <TableCell>{toCurrency(amount)}</TableCell>
                <TableCell>{status}</TableCell>
              </TableRow>
            )
          )}
        </TableBody>
      </Table>
    </>
  );
}

TransactionHistorySection.propTypes = {
  transactions: PropTypes.arrayOf(
    PropTypes.shape({
      createdAt: PropTypes.string,
      status: PropTypes.string,
      amount: PropTypes.number,
      paymentMethod: PropTypes.shape({
        _id: PropTypes.string,
        name: PropTypes.string
      })
    })
  )
};

TransactionHistorySection.defaultProps = {
  transactions: []
};
